.container {
  margin-bottom: 4rem;
}

.title {
  margin-bottom: 2rem;
  font-size: 3rem;
  font-weight: bold;
}

.subTitle {
  margin-bottom: 1.5rem;
  font-weight: 500;
}

.img {
  max-width: 100%;
  border-radius: 0.1rem;

  @media (max-width: 62rem) {
    margin-bottom: 2rem;
    width: 100%;
  }
}

.ingredientRow {
  margin-bottom: 2rem;
  // align-items: center;
}

.icon {
  margin-right: 1rem;
  font-size: 1.25rem;
}

.servingRow {
  margin-bottom: 1rem;
}

.instructionsRow {
  margin-bottom: 2rem;
}

.instructionsTitle {
  margin-bottom: 1.25rem;
}

.originalRecepieLink {
  opacity: 0.7;
  font-size: 0.7rem;
}

.instructionsContainer {
  background-color: #fff;
  padding: 2rem;
  border-radius: 0.2rem;
}

@media (max-width: 32rem) {
  .container {
    padding: 0 !important;
  }

  .instructionsContainer {
    padding: 1rem;
  }

  .title {
    font-size: 1.75em;
  }
}
